import React from "react";

// eslint-disable-next-line
import {
  Wrapper,

  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,

} from "./styles";

export default function ModalBank({ closeModal, data }) {
  return (


    <>
    <Wrapper  >
    
    <Details key={data.id} onClick={e => e.stopPropagation()} >
      
      <Title>	Banco

      <CloseButton aria-label="close modal" onClick={closeModal}>
         
          <AccessibleText>Close</AccessibleText>
        </CloseButton>
      </Title>
      <Divider mt="1rem" mb="1rem" />
      <TransactionContents>
        <List>
          <Item mb="0.5rem">
           <strong>BANCO:</strong>
            <TransactionAmount>
             {data.bank}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>NOME:</strong>
            <TransactionAmount>
             {data.name}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />


        <List>
          <Item mb="0.5rem">
           <strong>CPF:</strong>
            <TransactionAmount>
             {data.cpf}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>AG:</strong>
            <TransactionAmount>
             {data.ag}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>CONTA:</strong>
            <TransactionAmount>
             {data.conta}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />
       
      </TransactionContents>
    </Details>
  </Wrapper>
  <br />
</>
    
  );
}
