/*eslint-disable */
import React, { useEffect, useState } from 'react';
import api from '~/services/api';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Table } from 'react-bootstrap';

import ModalDash from '~/components/ModalDash';
import ModalTran from "~/components/ModalTran";
import ModalBank from "~/components/ModalBank";
import ModalReceber from "~/components/ModalReceber";

import Modal from '~/components/Modal';
import { Wrapper } from './styles';

export default function Dashboard() {
  const [email, setCPF] = useState('');
  const [userBenefi, setUserBenefi] = useState([]);
  const [userTran, setUserTran] = useState([]);
  const [bankUser, setBankUser] = useState([]);
  const [ReceberUser, setReceberUser] = useState([]);


  const [users, setUsers] = useState([]);
  // OPEN & CLOSE MODAL
  const [showModal, setShowModal] = useState(false);
  const [TranModal, setTranModal] = useState(false);
  const [BankModal, setBankModal] = useState(false);
  const [ReceberModal, setReceberModal] = useState(false);

  const closeReceber = () => setReceberModal(false);
  const openReceber = () => setReceberModal(true);
 
  const closeModalTran = () => setTranModal(false);
  const openModalTran  = () => setTranModal(true);

  const closeModalBank = () => setBankModal(false);
  const openModalBank  = () => setBankModal(true);
  
  const closeModal = () => setShowModal(false);
  const openModal = () => setShowModal(true);

  async function loadUsers() {
    api.get(`users?email=${email}`).then(response => {
      // traser users do data !

      setUsers(response.data.data);
    });
  }

  const loadInfo = async (ID, type) => {
    if (type == 'benefi') {
      const result = await api.get(
        `/bendtransactions/send/clients?user_id=${ID}`
      );
      setUserBenefi(result.data);
        openModal();
      
    } else if (type == 'tran') {
      const result = await api.get(`/transactions/send/clients?user_id=${ID}`);
      setUserTran(result.data);
      openModalTran();
    }else if (type == 'bank'){
      const result = await api.get(`/bank/send/clients?user_id=${ID}`);
      setBankUser(result.data);
      openModalBank();

    }else if(type == 'receber'){
      const result = await api.get(`/recend/send/clients?user_id=${ID}`);
      setReceberUser(result.data);
      openReceber();
    }
   
    
  };

  useEffect(() => {
    loadUsers();
  }, []);

  return (
    <>
      <Wrapper>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>Nome</th>
              <th>Email</th>
              <th>Telefone</th>
              <th>Beneficiário</th>
              <th>Transaction</th>
              <th>Receber</th>
              <th>Banco</th>
            </tr>
          </thead>
          <tbody>
            {users.map(user => {
              return (
                <tr key={user.id}>
                  <td>{user.name}</td>
                  <td>{user.email}</td>
                  <td>{user.phone}</td>
                  <td>
                    <button
                      onClick={() => {
                        loadInfo(user.id, 'benefi');
                      }}
                      type="button"
                      className="btn btn-warning"
                    >
                      {showModal && (
                        <Modal closeModal={closeModal} noCloseButton>
                          {userBenefi.map(beneficiario => (
                            // <ul key={beneficiario.id}>
                            //   <li>
                            //     Nome: <strong>{beneficiario.name}</strong>
                            //   </li>
                            //   <li>
                            //     Cod: <strong>{beneficiario.cod}</strong>
                            //   </li>
                            //   <li>
                            //     N. Conta:{' '}
                            //     <strong>{beneficiario.numberaccount}</strong>
                            //   </li>
                            //   <li>
                            //     Iban: <strong>{beneficiario.iban}</strong>
                            //   </li>
                            // </ul>
                            <ModalDash data={beneficiario} />
                          ))}

                        </Modal>
                      )}
                      Beneficiário
                    </button>
                  </td>
                  <td>
                    <button
                      onClick={() => {
                        loadInfo(user.id, 'tran');
                      }}
                      type="button"
                      className="btn btn-primary"
                    >
                       {TranModal && (
                        <Modal closeModal={closeModalTran} noCloseButton>
                          {userTran.map(Transactions => (
                            // <ul key={beneficiario.id}>
                            //   <li>
                            //     Nome: <strong>{beneficiario.name}</strong>
                            //   </li>
                            //   <li>
                            //     Cod: <strong>{beneficiario.cod}</strong>
                            //   </li>
                            //   <li>
                            //     N. Conta:{' '}
                            //     <strong>{beneficiario.numberaccount}</strong>
                            //   </li>
                            //   <li>
                            //     Iban: <strong>{beneficiario.iban}</strong>
                            //   </li>
                            // </ul>
                            <ModalTran data={Transactions} />
                          ))}

                        </Modal>
                      )}
                      Transaction
                    </button>
                  </td>
                  <td>
                    <button
                      onClick={() => {
                        loadInfo(user.id, 'receber');
                      }}
                      type="button"
                      className="btn btn-danger"
                    >
                       {ReceberModal && (
                        <Modal closeModal={closeReceber} noCloseButton>
                          {ReceberUser.map(receber => (
                            // <ul key={beneficiario.id}>
                            //   <li>
                            //     Nome: <strong>{beneficiario.name}</strong>
                            //   </li>
                            //   <li>
                            //     Cod: <strong>{beneficiario.cod}</strong>
                            //   </li>
                            //   <li>
                            //     N. Conta:{' '}
                            //     <strong>{beneficiario.numberaccount}</strong>
                            //   </li>
                            //   <li>
                            //     Iban: <strong>{beneficiario.iban}</strong>
                            //   </li>
                            // </ul>
                            <ModalReceber data={receber} />
                          ))}

                        </Modal>
                      )}
                      Receber
                    </button>
                  </td>
                  <td>
                    <button
                      onClick={() => {
                        loadInfo(user.id, 'bank');
                      }}
                      type="button"
                      className="btn btn-secondary"
                    >
                       {BankModal && (
                        <Modal closeModal={closeModalBank} noCloseButton>
                          {bankUser.map(bank => (
                            // <ul key={beneficiario.id}>
                            //   <li>
                            //     Nome: <strong>{beneficiario.name}</strong>
                            //   </li>
                            //   <li>
                            //     Cod: <strong>{beneficiario.cod}</strong>
                            //   </li>
                            //   <li>
                            //     N. Conta:{' '}
                            //     <strong>{beneficiario.numberaccount}</strong>
                            //   </li>
                            //   <li>
                            //     Iban: <strong>{beneficiario.iban}</strong>
                            //   </li>
                            // </ul>
                            <ModalBank data={bank} />
                          ))}

                        </Modal>
                      )}
                      Banco
                    </button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </Wrapper>
    </>
  );
}
