import React from "react";

// eslint-disable-next-line
import {
  Wrapper,

  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,

} from "./styles";

export default function ModalDash({ closeModal, data }) {
  return (
    // <>
    // <Wrapper onClick={e => e.stopPropagation()}>
    
    //   <Details>
        
    //   <ul key={data.id}>
    //   <li>
    //     NOME: <strong>{data.name}</strong>
    //   </li>
    //   <li>
    //     COD: <strong>{data.cod}</strong>
    //   </li>
    //   <li>
    //     N. CONTA:{' '}
    //     <strong>{data.numberaccount}</strong>
    //   </li>
    //   <li>
    //     IBAN: <strong>{data.iban}</strong>
    //   </li>
    // </ul>
    //   </Details>

    // </Wrapper>
    // < br />
    // </>

    <>
    <Wrapper  >
    
    <Details key={data.id} onClick={e => e.stopPropagation()} >
      
      <Title>	Beneficiário

      <CloseButton aria-label="close modal" onClick={closeModal}>
         
          <AccessibleText>Close</AccessibleText>
        </CloseButton>
      </Title>
      <Divider mt="1rem" mb="1rem" />
      <TransactionContents>
        <List>
          <Item mb="0.5rem">
           <strong>NOME:</strong>
            <TransactionAmount>
             {data.name}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>COD:</strong>
            <TransactionAmount>
             {data.cod}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />


        <List>
          <Item mb="0.5rem">
           <strong>N. CONTA:</strong>
            <TransactionAmount>
             {data.numberaccount}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>IBAN:</strong>
            <TransactionAmount>
             {data.iban}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />
       
      </TransactionContents>
    </Details>
  </Wrapper>
  <br />
</>
    
  );
}
