import React from "react";

// eslint-disable-next-line
import {
  Wrapper,

  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,

} from "./styles";

export default function ModalReceber({ closeModal, data }) {
  return (
    // <>
    // <Wrapper onClick={e => e.stopPropagation()}>
    
    //   <Details>
        
    //   <ul key={data.id}>
    //   <li>
    //     NOME: <strong>{data.name}</strong>
    //   </li>
    //   <li>
    //     COD: <strong>{data.cod}</strong>
    //   </li>
    //   <li>
    //     N. CONTA:{' '}
    //     <strong>{data.numberaccount}</strong>
    //   </li>
    //   <li>
    //     IBAN: <strong>{data.iban}</strong>
    //   </li>
    // </ul>
    //   </Details>

    // </Wrapper>
    // < br />
    // </>

    <>
    <Wrapper  >
    
    <Details key={data.id} onClick={e => e.stopPropagation()} >
      
      <Title>	Receber

      <CloseButton aria-label="close modal" onClick={closeModal}>
         
          <AccessibleText>Close</AccessibleText>
        </CloseButton>
      </Title>
      <Divider mt="1rem" mb="1rem" />
      <TransactionContents>
        <List>
          <Item mb="0.5rem">
           <strong>EMAIL:</strong>
            <TransactionAmount>
             {data.email}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>RECEBER:</strong>
            <TransactionAmount>
             {data.send}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />


        <List>
          <Item mb="0.5rem">
           <strong>PAGAR:</strong>
            <TransactionAmount>
             {data.recipient}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>MOEDAS A RECEBER:</strong>
            <TransactionAmount>
             {data.sendCurrency}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>MOEDAS A PAGAR:</strong>
            <TransactionAmount>
             {data.recipientCurrency}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />
      </TransactionContents>
    </Details>
  </Wrapper>
  <br />
</>
    
  );
}
