import React from "react";

// eslint-disable-next-line
import {
  Wrapper,

  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,

} from "./styles";

export default function ModalTran({ closeModal, data }) {
  return (

    <>

    <Wrapper key={data.id}  onClick={e => e.stopPropagation() }>
    
    <Details>
      
      <Title>	Transaction

      <CloseButton aria-label="close modal" onClick={closeModal}>
         
          <AccessibleText>Close</AccessibleText>
        </CloseButton>
      </Title>
      <Divider mt="1rem" mb="1rem" />
      <TransactionContents>
        <List>
          <Item mb="0.5rem">
           <strong>EMAIL:</strong>
            <TransactionAmount>
              {data.email}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>SEND:</strong>
            <TransactionAmount>
             {data.send}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />


        <List>
          <Item mb="0.5rem">
           <strong>RECIPIENT:</strong>
            <TransactionAmount>
             {data.recipient}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>MOEDA ENVIADA:</strong>
            <TransactionAmount>
             {data.sendCurrency}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />

        <List>
          <Item mb="0.5rem">
           <strong>MOEDA RECEBIDA:</strong>
            <TransactionAmount>
             {data.recipientCurrency}
            </TransactionAmount>
          </Item>
        </List>
        <Divider mt="1rem" mb="0.5rem" />
       
      </TransactionContents>
    </Details>
  </Wrapper>
  <br />
    </>
  );
}
