import styled from 'styled-components';

export const Container = styled.div`
  max-width: 500px;
  margin: 60px 160px;
  color: #fff;
  display: auto;
 
`;


export const Wrapper = styled.div`
  display: flex;
  position: relative;
  justify-content: center;
  background-color: #ffff;
  margin: auto;
  align-items: center;
  padding: 3.5rem 0;
  margin-top: 78px;
  margin-fixed: 12px

  @media (min-width: 992px) {
    padding: "6.5rem 0"};
  }
`


export const Contents = styled.div`
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: 576px) {
    max-width: 540px;
  }

  @media (min-width: 768px) {
    max-width: 720px;
  }

  @media (min-width: 992px) {
    max-width: 960px;
  }

  @media (min-width: 1200px) {
    max-width: 1170px;
  }
`